<template>
  <div class="detail">
    <MHeader :back="true"></MHeader>
    <div>
      <ul>
        <li>
          <label for="bookName"></label>
          <input type="text" v-model="book.bookName" id="bookName">
        </li>
        <li>
          <label for="bookInfo"></label>
          <input type="text" v-model="book.bookInfo" id="bookInfo">
        </li>
        <li>
          <label for="bookPrice"></label>
          <input type="text" v-model.number="book.bookPrice" id="bookPrice">
        </li>
        <li>
          <button @click="update"></button>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {findOneBook,updateBook} from '../api';
  import MHeader from "../base/MHeader";

  export default {
    data() {
      return {book: {}}
    },
    watch: {
      $route() { //
        this.getData();
      }
    },
    created() { // id 
      this.getData();
    },
    methods: {
      async update(){ // await 
        await updateBook(this.bid,this.book);
        this.$router.push('/list'); //
      },
      async getData() { //id book
        this.book = await findOneBook(this.bid);

        // 
        if (this.book !== null || typeof this.book !== 'undefined') {
          Object.keys(this.book).length > 0 ? void 0 : await this.$router.push('/list');
        }
      }
    },
    name: 'Detail',
    components: {
      MHeader
    },
    computed: {
      bid() {
        return this.$route.params.bid; //idbid
      }
    }
  }
</script>

<style scoped lang="less">
  ul {
    margin: 50px 10px 0 10px;
    li {
      label{
        font-size: 25px;
      }
      input{
        margin: 10px 0;
        height: 25px;
        width: 100%;
      }
      button {
        display: block;
        width: 60px;
        height: 35px;
        background: #00ccff;
        color: #ffffff;
        border: none;
        border-radius: 4px;
        outline: none; /**/
      }
    }
  }

  .detail {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: #fff;
    z-index: 100;
  }
</style>



