<template>
  <div>
    <MHeader></MHeader>
    <div class="content" ref="scroll" @scroll="loadMore">
      <ul>
        <router-link v-for="(book,index) in books"
                     :to="{name:'detail',params:{bid:book.bookId}}"
                     :key="index"
                     tag="li">
          <img v-lazy="book.bookCover">
          <div>
            <H4>{{book.bookName}}</H4>
            <p>{{book.bookInfo}}</p>
            <b>{{book.bookPrice}}</b>

            <div class="btn-list">
              <button @click.stop="remove(book.bookId)"></button>
              <button @click.stop></button>
            </div>

          </div>
        </router-link>
      </ul>
      <div @click="more" class="more"></div>
    </div>
  </div>
</template>

<script>
  import {pagination, removeBook} from '../api';
  import MHeader from "../base/MHeader";

  export default {
    //
    mounted() {
      let scroll = this.$refs.scroll; //
      let top = scroll.offsetTop;
      let distance = 0;
      let isMove = false;
      scroll.addEventListener('touchstart', (e) => {
        //   
        if (scroll.scrollTop != 0 || scroll.offsetTop != top) return;
        let start = e.touches[0].pageY; //
        let move = (e) => {
          isMove = true;
          let current = e.touches[0].pageY;
          distance = current - start; // 
          if (distance > 0) { //50 50
            if (distance <= 50) {
              scroll.style.top = distance + top + 'px';
            } else {
              distance = 50;
              scroll.style.top = top + 50 + 'px'
            }
          } else {
            //moveend
            scroll.removeEventListener('touchmove', move);
            scroll.removeEventListener('touchend', end);
          }
        }
        let end = (e) => {
          if (!isMove) return;
          isMove = false;
          clearInterval(this.timer1);
          this.timer1 = setInterval(() => { // distance 
            if (distance <= 0) {
              clearInterval(this.timer1);
              distance = 0;
              scroll.style.top = top + 'px';
              scroll.addEventListener('touchmove', move);
              scroll.addEventListener('touchend', end);
              this.books = []; //
              this.offset = 0;
              this.getData();
              return;
            }
            distance -= 1;
            scroll.style.top = top + distance + 'px';
          }, 1)
        }
        scroll.addEventListener('touchmove', move);
        scroll.addEventListener('touchend', end);
      }, false)
      console.log(top)
    },
    created() {
      this.getData();
    },
    name: 'List',
    components: {
      MHeader,
      pagination
    }, methods: {
      loadMore() {
        //   
        // scrolln 
        clearTimeout(this.timer); // 
        this.timer = setTimeout(() => {
          console.log(100)
          let {scrollTop, clientHeight, scrollHeight} = this.$refs.scroll;
          if (scrollTop + clientHeight + 20 > scrollHeight) {
            this.getData(); //
          }
        }, 60)

      },
      more() {
        this.getData();
      },
      async remove(id) {
        await removeBook(id); //
        //
        this.books = this.books.filter(item => item.bookId !== id);
      },
      async getData() {
        //
        if (this.hasMore && !this.isLoading) {
          this.isLoading = true;
          let {hasMore, books} = await pagination(this.offset);
          this.books = [...this.books, ...books];
          this.hasMore = hasMore;
          this.isLoading = false; //
          this.offset = this.books.length; // 
        }
      }
    }, data() {
      //offset hasMore  
      return {books: [], offset: 0, hasMore: true, isLoading: false, timer: Object}
    }
  }
</script>

<style scoped>
  .content ul {
    padding: 10px;
  }

  .content ul li {
    display: flex;
    padding: 10px 0;
    border-bottom: 1px solid #f1f1f1;
  }

  .content ul li image {
    width: 100px;
    height: 120px;
  }

  .content h4 {
    font-size: 20px;
    line-height: 35px;
  }

  .content p {
    color: #2a2a2a;
    line-height: 25px;
  }

  .content b {
    color: red;
  }

  .content button {
    display: block;
    width: 60px;
    height: 25px;
    background: orangered;
    color: #ffffff;
    border: none;
    border-radius: 15px;
    outline: none; /**/
  }

  .more {
    margin: 10px;
    background: #2afedd;
    height: 30px;
    line-height: 30px;
    text-align: center;
    font-size: 20px;
  }
  .btn-list {
    display: flex;
    justify-content: space-around;
  }
</style>



