<template>
  <div>
    <MHeader></MHeader>
    <div class="content">
      <Loading v-if="loading"></Loading>
      <template v-else>
        <Swiper :swiperSlides="sliders"></Swiper>
        <div class="container">
          <h3></h3>
          <ul>
            <li v-for="hot in hotBooks">
              <img :src="hot.bookCover" alt="">
              <b>{{hot.bookName}}</b>
            </li>
          </ul>
        </div>
      </template>

    </div>
  </div>
</template>

<script>
  //1. 2. 3.
  import MHeader from "../base/MHeader";
  import Swiper from "../base/Swiper";
  import {getAll} from '../api';
  import Loading from '../base/Loading'
  export default {
    name: 'Home',
    components: {
      MHeader,
      Swiper,
      Loading
    },
    // async  await
    created() {
      //this.getSlider(); //
      //this.getHot(); //
      this.getData();
    }, methods: {
      async getData() {
        let [sliders, hotBooks] = await getAll(); //[sliders,books]
        this.sliders = sliders;
        this.hotBooks = hotBooks;
        //  
        this.loading = false;
      }



      /*async getHot() {
        this.hotBooks = await getHotBook();
      },
      async getSlider() {
        //data  

        //sliders
        this.sliders = await getSliders();
      }*/
    },
    data() {
      return {sliders: [], hotBooks: [], loading: true}
    }
  }
</script>

<style scoped lang="less">
  .h3 {
    color: #999;
    padding: 5px 0;
  }

  .container {
    width: 90%;
    margin: 0 auto;
  }

  ul {
    display: flex;
    flex-wrap: wrap; /**/
    padding-bottom: 10px;

    li {
      width: 50%;
      text-align: center;
      margin: 5px 0;

      img {
        width: 100%
      }
    }
  }
</style>



